body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

nav {
    background-color: rgb(8, 1, 54)
}

a {
    text-decoration: none;
    color: white
}

.navbar-brand {
    font-size: 24pt;
}

.nav-item {
    font-size: 14pt;
    float: inline-start;
}
.navbar-item:hover {
    font-weight: bold;
    color: white
}
.jumbotron {
    background-image: url(/static/media/background.2493819e.jpg);
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

